
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background: #fff;
}

header {
  background-color: lightblue;
  padding: 16px 32px;
  display: flex;
  justify-content: center;
}

.todoDivArea{
  display: flex;
  justify-content: center;
}

.todoDiv{
  padding: 20px;
  width: 250px;
  border: 1px solid lightblue;
  border-radius: 30px;
  margin-top: 20px;
}

.todoText{
  border: none;
  margin-bottom: 20px;
}

.todoButton{
  width: 100px;
}

.contentDiv{
  border: 1px solid lightblue;
  border-radius: 30px;
  min-width: 250px;
  text-align: center;
  padding:20px;
}

footer{
  position: absolute;
  bottom: 0;
  left:50%;
  transform: translate(-50%,0);
}